<template lang="html">
    <div class="container mt-5 px-2">
      <p>The <strong>26th UN Climate Change Conference of the Parties (COP26)</strong> took place in Glasgow on 31 October ‚Äì 12 November 2021.</p>
      <p>168 panels were streamed on YouTube. In these panels, many important stories were told.</p>
      <p>However, these videos don't boast with an exceptional large number of viewers. The overview below aims to help to find interesting panels to watch.</p>
      <p>Table contains two merged datasets:</p>
    <ol>
      <li><a target="_blank" href="https://ukcop26.org/the-conference/green-zone-programme-of-events/">COP26 program information</a> (title & organizer)</li>
      <li>links to <a target="_blank" href="https://www.youtube.com/c/COP26/videos">past YouTube streams</a> (views count as of 12 November 2021)</li>
    </ol>
    <p></p>
    <ul>
      <li>Panels are between 1 and 1.5 hours long</li>
      <li><i>Search</i> in description or <i>filter</i> by clicking on tags. Tags created from descriptions automatically using <a target="_blank" href="https://en.wikipedia.org/wiki/Tf%E2%80%93idf">td-idf</a></li>
      <li>Can't chose? Just watch <strong><a target="_blank" :href="data[randInt].YouTube">{{data[randInt].Text}}</a></strong></li>
    </ul>

    <div class="table-responsive ">
      <p>üé• = contains movies</p>
    <table class="table table-hover">
      <thead>
      <tr>
        <th scope="col"></th>
        <th scope="col">Title</th>
        <th scope="col" class="clickable" @click="sort('organizer')">Organizer &#8645;</th>
        <th scope="col">Tags  <span @click="tag = ''" class="badge bg-primary clickable">{{ tag }}</span></th>
        <th scope="col">Search in description: <input type="search" v-model="search"> </th>
        <th scope="col" class="clickable" @click="sort('views')">Views &#8645;</th>
        <th scope="col">Video</th>

      </tr>
    </thead>
    <tbody>
        <tr v-for="row in sortedTable" :key="data.ID">
            <CatalogueItem v-on:filterByTag="filterTable" :row="row" />
        </tr>
    </tbody>

  </table>
</div>
  <p class="text-muted">
    Data merging, cleaning and publishing excercise by <a href="http://homolova.sk" target="_blank">Ada Homolova</a>.
  </p>
</div>
</template>

<script>
import CatalogueItem from './CatalogueItem.vue'

export default {
  props: ['data'],
  components: {CatalogueItem},
  data() {
    return {
      currentSort:'views',
      currentSortDir:'desc',
      tag : '',
      search: '',
      randInt: 0
    }
  },

  mounted() {
    console.log(this.randInt)

    this.randInt = Math.floor(Math.random() * this.data.length)

    console.log(this.randInt)

  },

  methods:{
  sort(s) {
    //if s == current sort, reverse
    if(s === this.currentSort) {
      this.currentSortDir = this.currentSortDir==='asc'?'desc':'asc';
    }
    this.currentSort = s;
  },

  filterTable(value) {
    // console.log("parent " + value)
    this.tag = value
    //console.log(value)
  },
},

  computed:{
  filteredTable() {
          return this.data.filter(c => {
            if(this.tag == '') return true;
            return c.tags.indexOf(this.tag.toLowerCase()) >= 0;
          })
        },

  searchedTable() {
          return this.filteredTable.filter(c => {
            if(this.search == '') return true;
            return c.description.toLowerCase().indexOf(this.search.toLowerCase()) >= 0;
          })
        },

  sortedTable() {
    return this.searchedTable.sort((a,b) => {

      let modifier = 1;
      if(this.currentSortDir === 'desc') modifier = -1;
      if(a[this.currentSort] < b[this.currentSort]) return -1 * modifier;
      if(a[this.currentSort] > b[this.currentSort]) return 1 * modifier;

      return 0;
    });
  }
}


}
</script>

<style lang="css">

.clickable {
  cursor: pointer
}

@media (max-width: 767.98px) {
  .container {
    max-width: 100% !important;
  }
}

</style>
